body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing:border-box;
  margin: 0;
  padding: 0;}
html {
  font-size: 62.5%; /*usar 1rem para 10pixeles. 62.5% = 10px (en base al navegador)
  1rem = 10px
  1.6rem = 16px*/
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background:whitesmoke;}

.logo {  -webkit-align-self: center;  align-self: center;
  margin: 0px;}

.header-main{
  padding: 7px;
  font-family: sans-serif;
  border: solid;
  display: grid;
  grid-auto-flow: column;
  background-color: white;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  /* linear-gradient(rgba(7, 201, 23, 0.925), rgba(190, 223, 5, 0.5)); */
  background-image:  url(/tierrabuena/static/media/imgheader.01497fe9.jpg);
  /* background: linear-gradient(to bottom left, rgb(1, 8, 29) 50%, rgb(24, 20, 143) 50%); */
  margin: 1rem;}

.header-main h1{color: whitesmoke;
  font-size: 23px;
  margin: 25px 5px;
  font-weight: bold; }

.header-main h2{color: rgb(153, 95, 8);
  -webkit-align-self: flex-end;
          align-self: flex-end;
  font-size: 18px;
  margin: 16px 8px;
  font-weight: bold; }

.header-main p{text-align: center;
  color: rgb(37, 216, 31);
  font-size: 1.6rem;
  margin-top: 2px;}

.header-main a{display: -webkit-inline-flex;display: inline-flex;
  -webkit-align-self: flex-end;
          align-self: flex-end;}
  
.dropdown{    margin: 16px 5px;
  text-align-last: center;
  -webkit-align-self: center;
          align-self: center;}

.dropdown-item {font-size: 15px;}

.btn {background-color: none;
  color: aliceblue;
  font-size: 15px;}


