/* ---------------------------------------------------------------------------- */

.card-main{display: block;
  margin: 0 2rem;
    /* width: auto; */
  /* text-align: left; */}

.card-main div {height: -webkit-fit-content;height: -moz-fit-content;height: fit-content;
  color: whitesmoke;}

.carousel-main h2{    font-weight: bolder;
  color: aliceblue;
  font-size: 34px;}

  .card-main p {
    display: inline-block;
    margin: 30px 56px 8px 56px;
    max-width: 360px;
    max-height: 208px;
    text-align: justify;
    opacity: 0.3;
    font-size: small;
    vertical-align: top;}
.card-main video{  padding:0px;
      margin:0px;
      width:100%;
      height:37vh;}
  .coloresNat:hover p{
  opacity: 1;}
  .trabajoArt:hover p{
    opacity: 1;}
  .minimalist:hover p{
    opacity: 1;}

.carousel-tarj {
  margin: 2rem;}

.carousel-bill {
  margin: 1rem 2rem;}

/* ----------------------------------------------------------------------------------------------- */
@media only screen and (min-width: 538px) {
  .card-main{
    display: block;
    margin: 0 2rem;}
.card-main video{  padding:0px;
  margin:0px;
  width:100%;
  height:50vh;
  object-fit: cover;
}
.card-main p{margin: 0 18px 8px 18px;}
.trabajoArt{margin-top: 0;
      margin-left: 1rem;}
.minimalist{margin-top: 0;
    margin-left: 1rem;}
}

/* -------------------------------------------------------------------------------------------- */
@media only screen and (min-width: 650px) {
  .carousel-main{
    display: grid;
    grid-template-columns: 50% 50%;
    margin: 0px 0;
}
.card-main video{  padding:0px;
  margin:0px;
  width:100%;
  height:70vh;
  object-fit: cover;
}
.carousel-bill {
  margin: 5px 2rem 5px 5px;
  
}
.carousel-tarj {
  margin: 5px 5px 5px 2rem;
}
}
/* ------------------------------------------------------------------------------ */
@media only screen and (min-width: 800px) {
  .card-main video{  padding:0px;
    margin:0px;
    width:100%;
    height:80vh;
    object-fit: cover;
  }
}
