*{box-sizing:border-box;
    margin: 0;
    padding: 0;}

.container{ min-width: 260px;
    text-align: -webkit-center;}

.card{margin: 8px 10px;
    min-width: 260px;
    text-align: -webkit-center;
    text-align-last: left;
    max-width: 200px;}

.card-text1{display: grid;
    margin-bottom: 0;
    grid-template-columns: repeat(2, 117px);
    font-size: 12px;}

.card-text1 ul{padding-left: 10px;
    font-size: 12px;
    list-style: none;}

.card-text li{font-size: 14px;
    list-style: none;}

.card-text p{margin-bottom: 0rem;
    text-align-last: right;
    font-size: 14px;}

.card-text-vis{text-align: justify;
    font-size: 12px!important;}

ol, ul {padding-left: 0rem;}
.btn{width: 100%;
    font-size: 13px;
    border-radius: 5px;
    color: ghostwhite;
    padding:4px ;
    font-weight: bold;
    text-align-last: auto;}
.fa {color:#25d366;
    font-size: 17px;}
.title{font-size: 20px;
    margin: 0 10px;
    border: solid;
    border-color: grey;
    border-width: 1px;
    text-align: center;}
.map{width:400px; 
    height:250px; 
    border:0; }
.card-text-cont{text-align: center;}
.form-group h3{text-align: left;}
.card-contact{
    min-width: 300px;}
/* ----------------------------------------------------------- */
@media screen and (min-width: 670px){
    .container {display: grid;
        grid-template-columns: repeat(2, 270px);
        justify-content: center;
        overflow: auto;
        /* grid-auto-flow: column; */
    }
.card-text-vis{font-size: 14px!important;}
.title{font-size: 22px;}
.map{width:500px; 
        height:350px; 
        border:0; }
.card-contact{min-width: 400px;}
}
/* --------------------------------------------------------------------------] */
@media screen and (min-width: 980px){
.container {display: grid;
    grid-template-columns: repeat(3, 270px);
    justify-content: center;
    overflow: auto;
    /* grid-auto-flow: column; */
}
.card-text{font-size: 12px;}
.card-text-vis{font-size: 15px!important;}
.title{font-size: 25px;}
}